.card-product {
    box-shadow: none;
    background: transparent;
    padding: 15px 0px 0px 0px;
  }

.last-column {
  font-size: 12px;
  font-weight: 500;
  padding-right: 6%;
}

.header-cont {
  display: flex;
  justify-content: space-between;
}

.rdt_TableHeader + header {
  display: block !important;
  min-height: 30px !important;
}

.rdt_TableHeader {
  min-height: 0px !important;
}

.cross {
  color: #ff0000;
}

.check {
  color: #00ff00;
  font-size: 19px;
}
.list-dmf-container {
  max-height: 45px;
  overflow-y: auto;
}

/* width */
.scroller::-webkit-scrollbar {
  width: 5px;
}

/* Track */
.scroller::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
.scroller::-webkit-scrollbar-thumb {
  background: #ffb131; 
  border-radius: 10px;
}

/* Handle on hover */
.scroller::-webkit-scrollbar-thumb:hover {
  background: #a68753; 
}

.map-container > header + div::-webkit-scrollbar {
  width: 5px;
}

.map-container > header + div::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}

.map-container > header + div::-webkit-scrollbar-thumb {
  background: #ffb131; 
  border-radius: 10px;
}

.map-container > header + div::-webkit-scrollbar-thumb:hover {
  background: #a68753; 
}




