.authentication__header {
  color: white;
  text-align: center;
}

.authentication-container {
  background-color: #080710;
  margin-top: -5rem;
  height: 100%;
  min-height: 100vh;
  background-image: url("../../images/bkg.jpeg");
  background-size: cover;
}

.authentication {
  width: 90%;
  max-width: 25rem;
  margin: 0 auto;
  text-align: center;
  position: inherit;
  overflow: visible;
  background: none;
}

.authentication.card {
  box-shadow: none;
}

.authentication form {
  margin-bottom: 1rem;
}

*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.background {
  width: 430px;
  height: 520px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}
.background .shape {
  height: 200px;
  width: 200px;
  position: absolute;
  border-radius: 50%;
}
.shape:first-child {
  background: linear-gradient(#1845ad, #23a2f6);
  left: -80px;
  top: -80px;
}
.shape:last-child {
  background: linear-gradient(to right, #ff512f, #f09819);
  right: -30px;
  bottom: -80px;
}
.authentication form {
  height: 100%;
  width: 400px;
  background-color: rgba(255, 255, 255, 0.13);
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
  padding: 50px 35px;
}
.authentication form * {
  font-family: "Poppins", sans-serif;
  color: #ffffff;
  letter-spacing: 0.5px;
  outline: none;
  border: none;
}
.authentication form h3 {
  font-size: 32px;
  font-weight: 500;
  line-height: 42px;
  text-align: center;
}

.authentication label {
  display: block;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 500;
}
.authentication input {
  display: block;
  height: 50px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.07);
  border-radius: 3px;
  padding: 0 10px;
  margin-top: 8px;
  font-size: 14px;
  font-weight: 300;
}
.authentication ::placeholder {
  color: #e5e5e5;
}
.authentication button {
  margin-top: 50px;
  width: 100%;
  background-color: #ffffff;
  color: #080710;
  padding: 15px 0;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  cursor: pointer;
}

.authentication .form-control--invalid input,
.authentication .form-control input,
.authentication .form-control input:focus,
.authentication .form-control input:is(:-webkit-autofill, :autofill) {
  background: rgba(255, 255, 255, 0.07);
}

.authentication-container .card {
  padding: 5rem 1rem;
}

.authentication-container .image-upload.center {
  border: 1px solid #465f66;
}
