.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3rem;
  height: 2.5px;
  background: white;
}

.main-navigation__title {
  color: white;
  height: inherit;
  padding: 10px 0px;
  text-align: center;
}

.main-navigation__title a {
  text-decoration: none;
  color: white;
  height: 100%;
}

.main-navigation__header-nav,
.large_screen {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}
img {
  height: inherit;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav,
  .large_screen {
    display: block;
  }
}

.main-navigation__drawer-nav .main-navigation__title {
  height: 150px;
}
